import React, {useContext, useState} from "react";
import {ModalContext} from "../../Context/ModalContext";
import "./ModalCatalog.scss";
import {Transition} from "react-transition-group";
import {Link} from "react-router-dom";

export const ModalCatalog = () => {
    const {modal} = useContext(ModalContext);
    const [selectedCategory, setSelectedCategory] = useState('electronic');

    const handleCategoryHover = (category: any) => {
        setSelectedCategory(category);
    };

    return (
        <>
            <Transition in={modal} timeout={300}>
                {(state) => (
                    <div className={`modalCatalog ${state}`}>
                        <div className="modalCatalog__container">
                            <ul className="modalCatalog__categories">
                                <li onMouseEnter={() => handleCategoryHover('electronic')}></li>
                                <li onMouseEnter={() => handleCategoryHover('clothes')}></li>
                                <li onMouseEnter={() => handleCategoryHover('shoes')}></li>
                                <li onMouseEnter={() => handleCategoryHover('furniture')}></li>
                            </ul>
                            <ul className={`modalCatalog__subcategories ${selectedCategory}`}>
                                {selectedCategory === 'electronic' && (
                                    <>
                                        <li><b>   </b></li>
                                        <Link to={'/catalog/'}>
                                            <li></li>
                                        </Link>
                                        <li></li>
                                        <li></li>
                                        <li></li>
                                    </>
                                )}
                            </ul>
                            <ul className={`modalCatalog__subcategories ${selectedCategory}`}>
                                {selectedCategory === 'electronic' && (
                                    <>
                                        <li><b>  </b></li>
                                        <Link to={'/catalog/'}>
                                            <li></li>
                                        </Link>
                                        <li></li>
                                        <li></li>
                                        <li></li>
                                    </>
                                )}
                                {selectedCategory === 'clothes' && (
                                    <>
                                        <li><b></b></li>
                                        <li></li>
                                        <li></li>
                                        <li></li>
                                        <li></li>
                                    </>
                                )}
                                {selectedCategory === 'shoes' && (
                                    <>
                                        <li><b></b></li>
                                        <li></li>
                                        <li></li>
                                        <li></li>
                                        <li></li>
                                    </>
                                )}
                                {selectedCategory === 'furniture' && (
                                    <>
                                        <li></li>
                                        <li></li>
                                        <li></li>
                                        <li></li>
                                        <li></li>
                                    </>
                                )}
                            </ul>
                        </div>
                    </div>
                )}
            </Transition>
        </>
    );
};
